import client from '../clients/eventsClient';

class EventFactory {
  static createSingleEvent(event, dates) {
    return {
      popover: {
        label: event.title,
        slot: 'event',
      },
      highlight: {
        backgroundColor: '#ff8080',
        borderColor: '##ff8080',
        borderWidth: '2px',
        borderStyle: 'solid',
      },
      customData: {
        event,
        start: dates
      },
      dates,
    }
  }

  static createLongEvent(event, start, end) {
    const dates = [
      {
        start,
        end
      }
    ];
    return {
      popover: {
        label: event.title,
        slot: 'event',
      },
      highlight: {
        backgroundColor: '#ff165c',
        borderColor: '##ff8080',
        borderWidth: '2px',
        borderStyle: 'solid',
      },
      customData: {
        event,
        start,
        end
      },
      dates,
    }
  }

  static createEvent(event) {
    const startDate = new Date(event.startTime);
    const endDate = event.endTime ? new Date(event.endTime) : null;

    return endDate
      ? EventFactory.createLongEvent(event, startDate, endDate)
      : EventFactory.createSingleEvent(event, startDate)
  }
}

export default class CalendarBean {

  static calendarEvent(event) {
    return EventFactory.createEvent(event)
  }

  static get(year, month) {
    return client.get(year, month)
      .then((events) => {
        if (!events) {
          return []
        }
        return events.map(CalendarBean.calendarEvent)
      })
  }
}
