<template>
  <div class="col-md-6 col-sm-12">
    <div class="text-content">

      <h2 :class="classes" class="event-title pt-2">
        {{ title }}
      </h2>

      <div class="event-section-text">
        <p :class="classes" class="event-text pt-3">
          {{ new Date(start) | dateFormat('D. M. H:mm') }}
          <span class="badge badge-pill badge-secondary p-2 m-2">
            {{ category }}
          </span>
        </p>
      </div>
      <div class="event-section-text">
        <p :class="classes" class="event-text pt-3">
          {{ annotation }}
        </p>
      </div>
      <div :class="classes" class="event-section-btn pt-3 pb-3">
        <a class="btn btn-light btn-md sr-button"
           :href="url + '#content'">Více</a></div>
    </div>
  </div>
</template>

<script>
  // todo base url
  export default {
    props: {
      title: {
        type: String,
        required: true
      },
      start: {
        type: String,
        required: true
      },
      category: {
        type: String,
        required: true
      },
      annotation: {
        type: String,
        required: true
      },
      url: {
        type: String,
        required: true
      },
      side: {
        type: String,
        required: true
      }
    },
    computed: {
      classes() {
        return this.side === 'left' ?
          ['text-align-left'] :
          ['text-align-right']
      }
    }
  }
</script>
