<template>
  <section class="bg-primary" id="events">
    <div class="container">
      <div class="row mb-4">
        <div class="col-lg-8 mx-auto text-center">
          <h2 class="section-heading text-white display-3">
            {{ title }}
          </h2>
        </div>
      </div>
      <div class="row">
        <event-item v-for="(event, index) in events"
                    :key="index"
                    :event="event"
                    :side="(index % 2) === 0 ? 'left' : 'right'">
        </event-item>
      </div>
      <div class="row text-center">
        <div class="pt-3 pb-3 col-12">
          <a @click="loadMore" class="btn btn-light btn-md sr-button">
            Načíst další ...
          </a>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import { AsyncDataMixin } from 'vue-async-data-2';
  import parse from 'url-parse';

  import TopNEventsBean from './TopNEventsBean'
  import EventItem from './EventItem';
  import EventDate from "../../../utils/EventDate";

  export default {
    mixins: [AsyncDataMixin],
    props: {
      title: {
        type: String,
        required: true
      },
      n: {
        type: Number,
        default: 10
      }
    },
    components: {
      EventItem
    },
    data() {
      return {
        events: [],
        items: this.n
      }
    },
    asyncData: {
      events() {
        return new TopNEventsBean(new EventDate()).fetch(this.items)
          .catch(e => {
            reject(e)
          });
      }
    },
    methods: {
      loadMore() {
        this.items = this.items + this.n;
        this.asyncReload('events')
      }
    }
  }
</script>
