<template>
  <div v-cloak>
    <div :style="fieldStyle">
      <div class="anim slideInUp">
        <gcse:search v-pre></gcse:search>
      </div>
    </div>
    <div :style="buttonStyle" class="p-3">
      <div class="gsc-search-opener" @click="opened = true">
        <button type="button" class="btn btn-light btn-outline-dark float-right">
          <i class="fas fa-search fa-xs"></i>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        opened: false
      }
    },
    mounted() {
      this.$nextTick(function () {
        var cx = '012110265351671576307:em27sw6lj6g';
        var gcse = document.createElement('script');
        gcse.type = 'text/javascript';
        gcse.async = true;
        gcse.src = 'https://cse.google.com/cse.js?cx=' + cx;
        var s = document.getElementsByTagName('script')[0];
        s.parentNode.insertBefore(gcse, s);
      })
    },
    computed: {
      fieldStyle() {
        return {
          display: this.opened ? 'block' : 'none'
        }
      },
      buttonStyle() {
        return {
          display: !this.opened ? 'block' : 'none'
        }
      }
    }
  };
</script>
